<template>
  <div class="main">
    <div class="flex top">
      <ul class="tab">
        <li :class="{active:tab == 1}" @click="tab = 1">地图查看</li>
        <li :class="{active:tab == 2}" @click="tab = 2">列表查看</li>
      </ul>
      <div class="flex user">
        <div class="flex" @click="user()">
          <img src="@/assets/icon/me.png" alt="">
          <span>{{info.name}}</span>
        </div>
        <div class="info">
          <span>{{info.position}}</span>
          <b>|</b>
          <span>{{info.company}}</span>
        </div>
      </div>
    </div>
    <div class="flex contain">
      <section>
        <div class="map_layout" v-show="tab == 1">
          <div id="map" class="example"></div>
          <button class="full_screen" type="button" @click="toggle">{{screenSize}}</button>
        </div>
        <template v-if="tab == 2" >
          <div class="search flex">
            <el-select v-model="type" placeholder="请选择" @change="select">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <div class="flex cont">
              <i class="el-icon-search"></i>
              <el-input v-model="input" placeholder="请输入内容" @change="change"></el-input>
            </div>
            <div class="group">
              <el-radio-group v-model="radio">
                <el-radio label="1">全部</el-radio>
                <el-radio label="2">区块</el-radio>
                <el-radio label="3">井队</el-radio>
                <el-radio label="4">井号</el-radio>
              </el-radio-group>
            </div>
          </div>
          <el-table :data="tabledata" style="width: 100%" @row-click="row">
            <el-table-column prop="status" label="状态" ></el-table-column>
            <el-table-column prop="well_no" label="井号" ></el-table-column>
            <el-table-column prop="area" label="区块" ></el-table-column>
            <el-table-column prop="oilfield" label="公司" ></el-table-column>
            <el-table-column prop="operate_unit" label="施工单位"></el-table-column>
            <el-table-column prop="operate_team" label="井队"></el-table-column>
            <el-table-column prop="design_start_at" label="开钻时间" width="160"></el-table-column>
            <el-table-column prop="design_depth" label="设计井深"></el-table-column>
            <el-table-column prop="current_depth" label="当前井深"></el-table-column>
            <el-table-column prop="footage" label="日进尺"></el-table-column>
            <el-table-column prop="current_layer" label="钻达层次"></el-table-column>
            <el-table-column prop="description" label="施工简况"></el-table-column>
            <el-table-column prop="density" label="密度(g/cm3)" width="100"></el-table-column>
            <el-table-column prop="viscosity" label="粘度"></el-table-column>
            <el-table-column prop="drilling_monitor" label="监督"></el-table-column>
            <el-table-column prop="well_control" label="井控类别"></el-table-column>
          </el-table>
        
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page=current
            :page-sizes="[5, 10, 15, 20]"
            :page-size=size
            layout="total, sizes, prev, pager, next, jumper"
            :total=total>
          </el-pagination>
        </template>
      </section>
      <aside>
        <ul class="bean" v-if="obj.well_no">
          <li>井号：{{obj.well_no}}</li>
          <li>区块：{{obj.area}}</li>
          <li>公司：{{obj.oilfield}}</li>
          <li>施工单位：{{obj.operate_unit}}</li>
          <li>井队：{{obj.operate_team}}</li>
          <li>开钻时间：{{obj.design_start_at}}</li>
          <li>设计井深：{{obj.design_depth}}</li>
          <li>当前井深：{{obj.current_depth}}</li>
          <li>日进尺：{{obj.footage}}</li>
          <li>井控类别：{{obj.well_control}}</li>
        </ul>
        <div class="flex icon" v-if="obj.well_no">
          <div class="item" @click="link(item.url)" v-for="(item, index) in icon" :key="index">
            <img :src="item.img" alt="">
            <div>{{item.txt}}</div>
          </div>
        </div>
      </aside>
    </div>
  </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import {getList} from '@/api/home'
  export default {
  data() {
    return {
      current: 1,
      size: 10,
      tab: 1,
      icon: [
        {
          txt: "井基础信息",
          img: require("@/assets/icon/1.png"),
          url: "/well"
        },
        {
          txt: "井眼轨迹",
          img: require("@/assets/icon/2.png"),
          url: "/trail"
        },
        {
          txt: "生产数据记录",
          img: require("@/assets/icon/3.png"),
          url: "/record"
        },
        {
          txt: "报表管理",
          img: require("@/assets/icon/4.png"),
          url: "/report"
        },
        {
          txt: "技术支持",
          img: require("@/assets/icon/5.png"),
          url: "/technical"
        },
        {
          txt: "随钻跟踪",
          img: require("@/assets/icon/6.png"),
          url: "/tracking"
        },
        {
          txt: "单井统计",
          img: require("@/assets/icon/7.png"),
          url: "/singlewell"
        },
        {
          txt: "完井数据",
          img: require("@/assets/icon/8.png"),
          url: "/wellcompletion"
        },
        {
          txt: "复杂及故障",
          img: require("@/assets/icon/9.png"),
          url: "/complexfault"
        }
      ],
      list: [],
      arr: [],
      index: 0,
      input: '',
      radio: '1',
      type: '',
      options: [
        {
          value: '1',
          label: '全部'
        }, 
        {
          value: '2',
          label: '正钻'
        }, 
        {
          value: '3',
          label: '完钻'
        },
        {
          value: '4',
          label: '完井'
        }
      ],
      screenSize: '全屏'
    };
  },

  components: {},

  computed: {
    ...mapGetters({
      info: 'info'
    }),
    tabledata: function() {
      let start = (this.current - 1) * this.size;
      let end = start + this.size;
      return this.arr.slice(start,end);
    },
    total: function() {
      return this.arr.length;
    },
    obj: function() {
      return this.list[this.index] || {}
    }
  },

  mounted: function() {
    getList({page_num: -1,page_size: 2}).then(res => {
      this.list = this.arr = res.well_data;
      this.map(res.well_data)
    })
    
  },

  methods: {
    handleSizeChange(val) {
      this.size = val;
    },
    handleCurrentChange(val) {
      this.current = val;
    },
    select(val) {
      if(val == 1) {
        this.arr = this.list;
      }else {
        let search = '正钻';
        if(val == 3) {
          search = '完钻'
        }else if(val == 4) {
          search = '完井'
        }
        this.arr = this.list.filter(item => item.status == search);
      }
    },
    change(val) {
      if(this.radio == 1) {
        // console.log('全部')
        this.arr = this.list.filter(item => {
          return item.area.indexOf(val) != -1 ||
          item.operate_team.indexOf(val) != -1 ||
          item.well_no.indexOf(val) != -1
        })
      }else if(this.radio == 2) {
        // console.log('区块')
        this.arr = this.list.filter(item => item.area.indexOf(val) != -1)
      }else if(this.radio == 3) {
        // console.log('井队')
        this.arr = this.list.filter(item => item.operate_team.indexOf(val) != -1)
      }else if(this.radio == 4) {
        // console.log('井号')
        this.arr = this.list.filter(item => item.well_no.indexOf(val) != -1)
      }
    },
    row(row, event, column) {
      let route = {well_no:row.well_no,well_hole: row.well_hole};
      this.$router.push('/report');
      this.$store.commit('SET_ROUTE',route);
    },
    user() {
      this.$router.push('/user');
    },
    link(url) {
      let obj = this.obj;
      let route = {well_no:obj.well_no,well_hole: obj.well_hole};
      this.$router.push(url);
      this.$store.commit('SET_ROUTE',route);
    },
    map(data) {
      let map =new BMap.Map('map');//lng: 114.3162001, lat: 30.58108413 116.404, 39.915
      let point = new BMap.Point(114.3162001, 30.58108413);
      let that = this;
      map.centerAndZoom(point, 11);
      map.addControl(new BMap.Control(      //添加控制控件 
        {anchor: BMAP_ANCHOR_BOTTOM_RIGHT}  //控件将定位到地图的右下角
      ));   
      map.addControl(new BMap.NavigationControl());    
      map.addControl(new BMap.ScaleControl());    
      map.addControl(new BMap.MapTypeControl());    
      map.enableScrollWheelZoom(true);
      var geolocation = new BMap.Geolocation();
      geolocation.getCurrentPosition(function(r){
        if(this.getStatus() == BMAP_STATUS_SUCCESS){
          var mk = new BMap.Marker(r.point);
          map.addOverlay(mk);
          map.panTo(r.point);
          mk.enableDragging();
          mk.setAnimation(BMAP_ANIMATION_BOUNCE);
          data.map((item,i) => {
            // let marker = new BMap.Marker({
            //   lng: item.x_axis,
            //   lat: item.y_axis
            // })
            let marker = new BMap.Marker(new BMap.Point(item.x_axis, item.y_axis))
            map.addOverlay(marker);
            marker.addEventListener("click", function(e){
              that.index = i;
            })
          })
        }else {
          this.$message.error('浏览器地位失败');
        }        
      });
    },
    toggle () {
      this.$fullscreen.toggle(this.$el.querySelector('.example'), {
        wrap: false,
        callback: this.fullscreenChange
      })
    },
    fullscreenChange (fullscreen) {
      this.fullscreen = fullscreen
    }
  }
  };
</script>

<style lang='scss' scoped>
.main {
  background: rgba(33,33,33,.9);
  min-width: 1080px;
  width: 90%;
  height: calc(100% - 40px);
  padding: 20px;
  margin: 20px auto;
  box-sizing: border-box;
}
.top {
  height: 50px;
  margin-bottom: 10px;
  align-items: center;
  .tab {
    li {
      width: 80px;
      line-height: 40px;
      text-align: center;
      float: left;
      margin-right: 20px;
      color: #fff;
      font-size: 18px;
      cursor: pointer;
      &.active {
        border-bottom: 3px solid #fff;
      }
    }
  }
  .user {
    color: #fff;
    .flex {
      cursor: pointer;
    }
    img {
      width: 22px;
      height: 22px;
      margin-right: 10px;
    }
    .info {
      margin-left: 20px;
    }
  }
}
.search {
  margin: 10px 0;
  justify-content: flex-start;
  align-items: center;
  .el-select {
    margin-right: 40px;
  }
  
}
.contain {
  height: calc(100% - 60px);
  color: white;
  section {
    background: rgba(44, 118, 154, 0.4);
    width: calc(100% - 410px);
    overflow: auto;
  }
  aside {
    background: rgba(44, 118, 154, 0.4);
    width: 400px;
    padding: 20px 50px;
    box-sizing: border-box;
    overflow: auto;
    .bean {
      font-size: 12px;
      li {
        margin-bottom: 10px;
      }
    }
    .icon {
      flex-wrap: wrap;
      .item {
        width: 30%;
        margin-top: 30px;
        color: #fff;
        font-size: 12px;
        text-align: center;
        cursor: pointer;
        img {
          display: block;
          margin: 0 auto 12px;
        }
      }
    }
  }
}
.map_layout {
  width: 100%;
  height: 100%;
  position: relative;
  #map {
    height: 100%;
  }
  .full_screen {
    font-size: 12px;
    padding: 5px;
    color: yellow;
    background-color:rgba(0,0,0,0.4);
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 1000;
    cursor: pointer;
    border: none;
    outline:none; 
  }
}
</style>
<style lang="scss">
.contain tr {
  cursor: pointer;
}
.cont {
  align-items: center;
  border-bottom: 1px solid #fff;
  margin-right: 20px;
  .el-input input {
    background: transparent;
    border: none;
    color: #fff;
  }
}
.group {

  .el-radio {
    color: #fff;
  }
}
</style>



